body {
    font-size: 16px;
    line-height: 1.6;
    color: #333;
    overflow-x: hidden;
}

.container-fluid {
    margin: 0 auto;
    max-width: 1200px;
    padding: 0 15px;
}

.ant-layout {
    color: #f8f8ff;
    background: #222 !important;
}

.mainLayout .ant-layout-header {
    background: #222;
    padding: 0;
    height: auto;
    line-height: 1.6;
}

p {
    font-size: 16px;
    line-height: 1.6;
}

.bgGray {
    background-color: #333;
}

/* 
  title holder
  ------------
  */
.titleHolder {
    color: #f8f8ff;
    text-align: center;
    position: relative;
    padding: 0 0 20px;
    margin: 0 0 40px;
}

.titleHolder:after {
    transform: translateX(-50%);
    content: "";
    background: #83817d;
    position: absolute;
    bottom: 0;
    left: 50%;
    height: 3px;
    width: 50px;
}

.titleHolder h2 {
    color: #fda403;
    font-size: 28px;
    margin: 0;
    padding-top: 5vh;
}

/* 
  header
  ------ 
  */
.ant-layout-header {
    position: fixed;
    left: 0;
    right: 0;
    z-index: 999;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
}

.header .ant-anchor-wrapper {
    background: none;
}

.header .ant-anchor {
    display: flex;
}

.header .ant-anchor-ink {
    display: none;
}

.header .ant-anchor-link {
    padding: 0 20px;
    line-height: 1.4;
    position: relative;
}

.header .ant-anchor-link:before,
.header .ant-anchor-link:after {
    content: "";
    background: #1890ff;
    width: 1px;
    position: absolute;
    top: 2px;
    bottom: 2px;
    left: 30%;
    opacity: 0;
    transition: all 0.3s linear;
}

.header .ant-anchor-link:after {
    left: auto;
    right: 30%;
}

.header .ant-anchor-link.ant-anchor-link-active:before,
.header .ant-anchor-link:hover:before {
    left: 5%;
    opacity: 1;
}

.header .ant-anchor-link.ant-anchor-link-active:after,
.header .ant-anchor-link:hover:after {
    right: 5%;
    opacity: 1;
}

/* ant menu */
.header .ant-menu {
    border: 0;
    background: none !important;
}

.header .ant-menu li.ant-menu-item {
    position: relative;
    color: #333;
}

.header .ant-menu li.ant-menu-item:after {
    left: auto;
    right: 30%;
}

.header .ant-menu .menuLinks:hover {
    background-color: #fda403;
}

.header .ant-menu .menuLinks:focus {
    background-color: #fda403 !important;
}
/* 
  hero
  ---- 
  */
.heroBlock {
    background: url("./images/mohammad-alizade-4wzRuAb-KWs-unsplash.jpg")
        no-repeat;
    background-position: 50% 100%;
    background-size: cover;
    height: 100vh;
}

.heroBlock .container-fluid {
    display: block !important;
}

.heroBlock .ant-carousel,
.heroBlock .ant-carousel div {
    height: 100%;
}

.heroBlock .slick-dots li button {
    background: #000 !important;
}

.heroBlock .slick-dots li.slick-active button {
    background: #1890ff !important;
}

.heroBlock .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 0 70px;
}

.heroBlock h1 {
    text-align: center;
    font-size: 7.5vh;
    line-height: 1.2;
    margin: 0 0 30px;
}

.heroBlock h4 {
    text-align: center;
    font-size: 2.5vh;
    margin: 0 0 30px;
}

.heroBlock .btnHolder {
    text-align: center;
    height: auto !important;
}

.btnHolder Button {
    color: #fff;
}

.btnHolder Button:hover {
    color: #333;
}

.heroBlock .btnHolder button,
.heroBlock .btnHolder .fas {
    margin: 0 10px 0 0;
}

/* 
  block
  ----- 
  */
.block {
    padding: 60px 0;
    border-bottom: 1px solid #d9d9d9;
}

/* 
  about
  ----- 
  */

.aboutBlock {
    background-color: #333;
    height: 100vh;
    padding: 13vh;
}
.aboutBlock .contentHolder {
    color: #f8f8ff;
    text-align: center;
    margin: 2vh 7.5vw;
}

.aboutBlock .icon {
    font-size: 40px;
    color: #fda403;
    margin: 0 0 10px;
}

.aboutBlock .content {
    text-align: center;
    padding: 2vh 5vw;
}

.aboutBlock .content h3 {
    color: #fda403;
}

.aboutBlock .content p {
    color: #f8f8ff;
}

/*
portfolio
*/

.featureBlock {
    /* height: 100vh; */
    margin-top: 5vh;
}

/* 
  contact
  -------
  */

.contactBlock {
    background-color: #333;
    height: 100vh;
    padding: 13vh;
}
.contactBlock .ant-form {
    max-width: 33vw;
    margin: auto;
}

.contactBlock .ant-form textarea {
    min-height: 150px;
    height: 150px;
}

.contactBlock .ant-form Button {
    background-color: #fda403;
    display: block;
    margin: 0 auto;
}

.contactBlock .ant-form Button:hover {
    color: #333;
}

/* 
  footer
  ------
  */
.ant-layout-footer {
    background: #111 !important;
    color: #fff !important;
    padding: 30px 0 !important;
    text-align: center;
}

.footer .logo {
    font-size: 22px;
    text-transform: uppercase;
    margin: 0 0 20px;
}

.footer .fas {
    color: #1890ff;
    font-size: 5vh;
    margin: 0 5px 0 0;
}

.footer .logo a {
    color: #fff;
}

.footer .socials {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    font-size: 24px;
}

.footer .socials li {
    margin: 0 20px;
}

.footer .socials .footerLinks {
    color: #fda403;
}

.footer .socials .footerLinks:hover {
    color: #fff;
}

.footer .goTop:hover {
    opacity: 0.7;
}

.mobileVisible {
    display: none;
}

.mobileHidden {
    display: block;
}

/* 
  responsive
  ----------
  */
@media only screen and (max-width: 991px) {
    .pricingBlock .ant-row > div:nth-child(2) .ant-list-item {
        transform: scale(1.14);
    }
}

@media only screen and (max-width: 767px) {
    .pricingBlock .ant-row > div:nth-child(2) .ant-list-item {
        transform: scale(1);
    }

    .mobileVisible {
        display: block;
    }

    .mobileHidden {
        display: none;
    }
}
